.author-div {
    margin: 30px 0px;
    display: flex;

    img {
        width: 70px;
        height: 70px;
        border-radius: 50px;
    }

    &__elements {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        &__text {
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin-left: 15px;
    
            h3 {
                font-family: 'Open Sans', sans-serif;
            }
        
            p {
                font-style: italic;
                font-size: 14px;
            }
        }

        .reload-button {
            display: flex;
            flex-direction: column;
            justify-content: center;

            button {
                background-color: transparent;
                border: none;
                cursor: pointer;
      
                &:hover {
                    svg {
                    fill: darken($color: #d3d9d3, $amount: 30%);
                    }
                }
            }
        }
    
    }
}